<template>
    <div>

        <el-dialog :title="(app.loginDialogShow ? '' : '') + ''" @close="authDialogclose" :visible="app.loginDialogShow || app.registerDialogShow" width="450px" class="auth_dialog">
            <el-form ref="loginForm" :model="loginForm" :rules="loginFormRules" v-if="app.loginDialogShow">
                <el-form-item label="" prop="username">
                    <el-input v-model="loginForm.username" autocomplete="off"></el-input>
                </el-form-item>
                <el-form-item label="" prop="password">
                    <el-input type="password" v-model="loginForm.password" autocomplete="off"></el-input>
                </el-form-item>                                 
                <div class="tips">
                    <a href="javascript:" @click="toRegister"></a>
                </div>
            </el-form>

            <el-form ref="registerForm" :model="registerForm" :rules="registerFormRules" v-if="app.registerDialogShow">
                <el-form-item label="" prop="username">
                    <el-input v-model="registerForm.username" clearable placeholder="6"></el-input>
                </el-form-item>
                <el-form-item label="" prop="password">
                    <el-input type="password" clearable v-model="registerForm.password" autocomplete="off" placeholder="6"></el-input>
                </el-form-item>
                <el-form-item label="" prop="rePassword">
                    <el-input type="password" clearable v-model="registerForm.rePassword" autocomplete="off" placeholder="6"></el-input>
                </el-form-item>
                <el-form-item label="" prop="code" v-if="!user.domain.invite_code && !localCode">
                    <el-input v-model="registerForm.code" @keyup.enter.native="authDialogSubmit" clearable placeholder=""></el-input>
                </el-form-item>
                <div class="tips">
                    <a href="javascript:" @click="toLogin"></a>
                </div>
            </el-form>

            <div slot="footer" class="dialog-footer">
                <el-button @click="authDialogclose"> </el-button>
                <el-button type="primary" :loading="loginRegisterLoading" @click="authDialogSubmit"> </el-button>
            </div>
        </el-dialog>


        <el-dialog
            title=""
            :visible.sync="app.addBankCardShow"
            width="30%"
        >
            <div class="new_card">
                <el-form ref="bankForm" :model="bank.form" :rules="bank.formRules" label-width="100px" label-position="top">
                    <el-form-item label="" prop="open_account_bank">
                        <el-select v-model="bank.form.open_account_bank" placeholder="">
                            <el-option
                            v-for="name in bank.bank_list"
                            :key="name"
                            :label="name"
                            :value="name">
                            </el-option>
                        </el-select>
                    </el-form-item>

                    <el-form-item label="" prop="areaSelect">
                        <el-cascader
                            change-on-select
                            expand-trigger="hover"
                            :options="app.areaOptions"
                            v-model="bank.form.areaSelect"
                        >
                        </el-cascader>
                    </el-form-item>
                    <el-form-item label="" prop="bank_account">
                        <el-input v-model="bank.form.bank_account" placeholder=""></el-input>
                    </el-form-item>

                    <el-form-item label="" prop="name">
                        <el-input v-model="bank.form.name" placeholder=""></el-input>
                    </el-form-item>

                </el-form>
            </div>
            <span slot="footer" class="dialog-footer">
                <el-button @click="$store.dispatch('closeAddBankCardDialog')"> </el-button>
                <el-button type="primary" @click="addBankCardSubmit" :loading="bank.loading"> </el-button>
            </span>
        </el-dialog>


        <el-dialog title="" class="detail_dialog" :visible.sync="app.scActivityDialogVisible">
            <el-form label-width="100px" label-position="left">
                <el-form-item label="">
                    888.88
                </el-form-item>
                <el-form-item label="">
                    
                </el-form-item>
                <el-form-item label="">
                    
                    <!--  -->
                    <a href="javascript:;" @click="toCharge"></a>
                </el-form-item>
                <el-form-item label="">
                    <el-table
                        size="small"
                        :data="[
                            { amount: 200, gold: 18.88, check: 5 + ''},
                            { amount: 500, gold: 58.88, check: 5 + ''},
                            { amount: 1000, gold: 108.88, check: 5 + ''},
                            { amount: 3000, gold: 328.88, check: 5 + ''},
                            { amount: 5000, gold: 568.88, check: 5 + ''},
                            { amount: 10000, gold: 888.88, check: 5 + ''},
                        ]"
                        border
                        style="width: 80%"
                    >
                        <el-table-column
                            prop="amount"
                            label=""
                        >
                        </el-table-column>
                        <el-table-column
                            prop="gold"
                            label=""
                        >
                        </el-table-column>
                        <el-table-column
                            prop="check"
                            label=""
                        >
                        </el-table-column>
                    </el-table>
                </el-form-item>
                <el-form-item label="">
                    <ul>
                        <li>1. 11</li>
                        <li>2. </li>
                        <li>3IP</li>
                        <li>4</li>
                        <li>5</li>
                        <li>6</li>
                    </ul>
                </el-form-item>
                <el-form-item label="">
                    <ul>
                        <li>18</li>
                        <li>2{{ app.siteInfo.name }}</li>
                        <li>3{{ app.siteInfo.name }}</li>
                        <li>4{{ app.siteInfo.name }}</li>
                        <li>5{{ app.siteInfo.name }}</li>
                    </ul>
                </el-form-item>
                <el-form-item>
                    <el-button type="success" @click="toCharge"></el-button>
                </el-form-item>
            </el-form>
        </el-dialog>

        

    </div>
</template>

<script>
import { mapGetters } from 'vuex'
import { 
    usernameValidator,
    passwordValidator,
    customCodeValidator,
} from '@/utils/validator'

export default {
  name: 'Global-Dialog',
  props: ['user', 'app'],
  data() {
        var validateArea = (rule, value, callback) => {
            if (!value || value.length != 2) {
                callback(new Error(''));
            } else {
                callback();
            }
        };

        var validateRePassword = (rule, value, callback) => {
            if(value == '') {
                callback(new Error(''));
            }
            else if (value != this.registerForm.password) {
                callback(new Error(''));
            } else {
                callback();
            }
        }

        return {
            loginForm: {
                username: '',
                password: '',
            },

            loginFormRules: {
                username: { require: true, validator: usernameValidator, trigger: 'blur'},
                password: passwordValidator,
            },

            registerForm: {
                username: '',
                password: '',
                rePassword: '',
                code: ''
            },

            registerFormRules: {
                username: { require: true, validator: usernameValidator, trigger: 'blur'},
                password: passwordValidator,
                rePassword: { require: true, validator: validateRePassword, trigger: 'blur'},
                code: customCodeValidator,
            },

            loginRegisterLoading: false,

            bank: {
                bank_list: [
                    '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', ''
                ],
                form: {
                    open_account_bank: '',
                    areaSelect: [],
                    bank_account: '',
                    name: '',
                },
                formRules: {
                    open_account_bank: [{ required: true, message: '', trigger: 'change' }],
                    areaSelect: [{ required: true, validator: validateArea, trigger: 'change' }],
                    bank_account: [{ required: true, message: '', trigger: 'blur' }],
                    name: [{ required: true, message: '', trigger: 'blur' }],
                },
                loading: false,
            },
        }
  },
  components: {

  },

  computed: {
      localCode() {
          return window.localStorage.getItem('code');
      },
  },

  mounted() {

  },
  methods: {
    toRegister() {
        this.$store.dispatch('closeLoginDialog');
        this.$store.dispatch('openRegisterDialog');
    },

    toLogin() {
        this.$store.dispatch('closeRegisterDialog');
        this.$store.dispatch('openLoginDialog');
    },

    authDialogclose() {
        this.$store.dispatch('closeLoginDialog');
        this.$store.dispatch('closeRegisterDialog');
    },

    authDialogSubmit() {
        if(this.app.loginDialogShow) {
            if(this.loginRegisterLoading) return ;

            this.$refs.loginForm.validate((valid) => {
                if(valid) {
                    this.loginRegisterLoading = true;
                    this.$store.dispatch('loginByUsername', {
                        username: this.loginForm.username, 
                        password: this.loginForm.password
                    }).then((json) => {
                        this.loginRegisterLoading = false;
                        // if(json.code == 200) {
                        //     this.$message({
                        //         message: '',
                        //         type: 'success'
                        //     });
                        // }
                    })
                }
            });
            
        }else if(this.app.registerDialogShow) {
            if(this.loginRegisterLoading) return ;

            if(this.user.domain.invite_code) {
                this.registerForm.code = this.user.domain.invite_code;
            }

            if(this.localCode) {
                this.registerForm.code = this.localCode;
            }

            this.registerForm.username = this.registerForm.username.trim();

            this.$refs.registerForm.validate((valid) => {
                if (valid) {

                    var data = {
                        username: this.registerForm.username,
                        password: this.registerForm.password,
                        code: this.registerForm.code,
                    };

                    if(this.registerForm.rePassword != this.registerForm.password) {
                        this.$message.error('');
                        return;
                    }

                    // this.loginRegisterLoading = true;
                    this.$store.dispatch('userRegister', data).then((json) => {
                        this.loginRegisterLoading = false;
                        if(json.code == 200) {
                            // this.$message({
                            //     message: '',
                            //     type: 'success'
                            // });

                            // this.$router.push({ path: '/' })
                        }
                    })
                } else {
                    console.log('');
                    return false;
                }
            });
        }
    },

    addBankCardSubmit() {
        this.$refs.bankForm.validate((valid) => {
            if (valid) {
                var data = {
                    name: this.bank.form.name,
                    open_account_bank: this.bank.form.open_account_bank,
                    bank_account: this.bank.form.bank_account,
                    open_account_province: this.bank.form.areaSelect[0] + '-' + this.bank.form.areaSelect[1],
                    open_account_address: '',
                    id_card: '',
                }

                if(this.bank.loading) return ;
                this.bank.loading = true;
                this.$store.dispatch('addBankcard', data).then(json => {
                    this.bank.loading = false;

                    if (json.code == 200) {
                        this.$message.success('');
                        this.bank.form = {
                            open_account_bank: '',
                            areaSelect: [],
                            bank_account: '',
                            name: '',
                        };
                        this.$store.dispatch('bankcardList');
                        this.$store.dispatch('closeAddBankCardDialog')
                    } else {
                        this.$message.error(json.info);
                    }
                })
            }
        })
    },

    toCharge() {
        this.$store.dispatch('closeScActivityDialog');
        this.$router.push('/user/charge');
    }
  }
}
</script>

